  display: block;
  width: 100%;
  padding-bottom: 58%;
}
.vidbox.type--url, .vidbox.type--URL {
  width: 100%;
  background: black;
}
.vidbox .fallback-image,
.vidbox [data-fallback-image] {
  position: absolute;
  top: 0;
  lefT: 0;
  width: 100%;
  height: 100%;
}
.vidbox .fallback-image img,
.vidbox [data-fallback-image] img {
  object-fit: cover;
}
.vidbox.video-banner {
  position: absolute;
  left: 0;
  top: 0;
