}
.vidbox.use-vid .vidbox__video {
  opacity: 1;
}
.vidbox.use-gif .fallback.fallback-gif {
  opacity: 1;
}
.vidbox iframe,
.vidbox video {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 130%;
  width: 150%;
  transform: translate3d(-50%, -50%, 0);
  opacity: 0;
  transition: opacity 400ms ease;
  min-width: 150vh;
  min-height: 80vw;
}
.tv svg {
  height: 100%;
