  .conversation {
    grid-template-columns: 330px 1fr 330px;
  }
}
.conversation .conversation__specialist, .conversation .conversation__customer {
  align-self: end;
  transition: transform 300ms cubic-bezier(0.76, 0, 0.24, 1);
}
.conversation .conversation__customer {
  transition-delay: 100ms;
}
.conversation .conversation__customer specialist-details h4, .conversation .conversation__customer specialist-details h5 {
  transition: transform 300ms 300ms cubic-bezier(0.76, 0, 0.24, 1);
}
.conversation .conversation__customer specialist-details h5 {
  transition-delay: 500ms;
}
.conversation__content {
  align-self: center;
  margin-bottom: 100px;
}
.conversation .specialist-details h4, .conversation .specialist-details h5 {
  transition: transform 300ms 200ms cubic-bezier(0.76, 0, 0.24, 1);
}
.conversation .specialist-details h5 {
  transition-delay: 400ms;
}
[data-status=pre] .conversation .conversation__specialist, [data-status=pre] .conversation .conversation__customer {
  transform: scale(0.9);
}
[data-status=pre] .conversation .specialist-details h4, [data-status=pre] .conversation .specialist-details h5 {
  transform: translateY(-150%);
}
[data-status=pre] .conversation .conversation__content {
  opacity: 0;
}
[data-status=post] .conversation .conversation__specialist, [data-status=post] .conversation .conversation__customer {
  transform: scale(0.59);
}
[data-status=post] .conversation .specialist-details h4, [data-status=post] .conversation .specialist-details h5 {
  transform: translateY(-150%);
}
[data-status=post] .conversation .conversation__content .message {
  transition: transform 1000ms cubic-bezier(0.76, 0, 0.24, 1);
}
[data-status=post] .conversation .conversation__content .message:nth-child(2) {
  transition-delay: 200ms;
}
[data-status=post] .conversation .conversation__content .message:nth-child(3) {
  transition-delay: 300ms;
}
[data-status=post] .conversation .conversation__content .message:nth-child(4) {
  transition-delay: 400ms;
}
[data-status=post] .conversation .conversation__content .message:nth-child(5) {
  transition-delay: 500ms;
}
[data-status=post] .conversation .conversation__content .message:nth-child(6) {
  transition-delay: 600ms;
}
[data-status=post] .conversation .conversation__content .message:nth-child(7) {
  transition-delay: 700ms;
}
[data-status=post] .conversation .conversation__content .message:nth-child(8) {
  transition-delay: 800ms;
}
.conversation__specialist {
  transform-origin: bottom left;
}
.conversation__customer {
