  position: relative;
  text-align: left;
  margin: 0 20px;
}
.message .speech-bubble::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0;
  height: 0;
  border: 16px solid transparent;
  border-right-color: #ffffff;
  border-left: 0;
  border-bottom: 0;
  margin-top: -8px;
  margin-left: -16px;
}
.message[data-author=customer] {
