  --star-color: #fff;
  --star-background: #fc0;
  --percent: 100%;
  transition: opacity 300ms ease;
  font-family: Times;
}
.stars .star {
  display: inline-block;
}
.stars::before {
  content: "";
  letter-spacing: 3px;
  background: linear-gradient(90deg, var(--star-background) var(--percent), var(--star-color) var(--percent));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: transform 400ms 400ms ease;
}

.introduction .intro-content {
  transition: opacity 400ms 200ms cubic-bezier(0.76, 0, 0.24, 1);
}
.introduction .intro-bg {
  transition: opacity 600ms 600ms cubic-bezier(0.76, 0, 0.24, 1);
}
.introduction[data-status=post] .intro-content,
.introduction[data-status=post] .intro-bg {
  opacity: 0;
}
.introduction[data-status=post] .intro-content > * {
  transition: transform 300ms 100ms cubic-bezier(0.76, 0, 0.24, 1);
  transform: translateY(30px);
}
.introduction[data-status=post] .intro-content > *:nth-child(2) {
  transition-delay: 200ms;
}
.introduction[data-status=post] .intro-content > *:nth-child(3) {
  transition-delay: 300ms;
}

.expanded .expanded-content {
  transition: opacity 400ms 200ms cubic-bezier(0.76, 0, 0.24, 1);
}

html:not(.no-js) [data-anim-in] {
  visibility: hidden;
}
html:not(.no-js) [data-anim-in-children] > * {
  visibility: hidden;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes pulse {
  from {
    opacity: 1;
  }
  to {
    opacity: 0.6;
  }
}
@keyframes backforth {
  0% {
    transform: translateX(0%);
  }
  25% {
    transform: translateX(12%);
  }
  50% {
    transform: translateX(0%);
  }
  75% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(0%);
  }
}
[data-rotate-me] {
  animation: rotate 30s linear infinite;
  transform-origin: 50% 50%;
}
[data-rotate-me][data-rotate-me=reverse] {
  animation: rotate 48s linear infinite reverse;
}

[data-pulse-me] {
  transform-origin: 50% 50%;
  animation: pulse 5s alternate-reverse linear infinite;
}

[data-back-forth] {
  position: relative;
  display: block;
  transform-origin: 50% 50%;
  animation: backforth 102s alternate-reverse linear infinite;
}
[data-back-forth]:nth-child(2), [data-back-forth] [data-back-forth="2"] {
  animation: backforth 63s alternate linear infinite;
}
[data-back-forth]:nth-child(3), [data-back-forth] [data-back-forth="3"] {
  animation-duration: 100s;
  animation-delay: 4s;
}
[data-back-forth]:nth-child(4), [data-back-forth] [data-back-forth="4"] {
  animation-duration: 95s;
  animation-direction: alternate;
}

